/*Lato*/

@font-face {
  /*Lato-Bold*/
  font-family: 'Lato';
  src: url_css('fonts/Lato/Bold/LatoBold.eot');
  src: url_css('fonts/Lato/Bold/LatoBold.eot#iefix') format('embedded-opentype'),
  url_css('fonts/Lato/Bold/LatoBold.woff2') format('woff2'),
  url_css('fonts/Lato/Bold/LatoBold.svg') format('svg'),
  url_css('fonts/Lato/Bold/LatoBold.ttf') format('truetype'),
  url_css('fonts/Lato/Bold/LatoBold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  /*Lato-Regular*/
  font-family: 'Lato';
  src: url_css('fonts/Lato/Regular/LatoRegular.eot');
  src: url_css('fonts/Lato/Regular/LatoRegular.eot#iefix') format('embedded-opentype'),
  url_css('fonts/Lato/Regular/LatoRegular.woff2') format('woff2'),
  url_css('fonts/Lato/Regular/LatoRegular.svg') format('svg'),
  url_css('fonts/Lato/Regular/LatoRegular.ttf') format('truetype'),
  url_css('fonts/Lato/Regular/LatoRegular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
/*ProximaNova*/
@font-face {
  /*ProximaNova-Bold*/
  font-family: 'Proxima Nova';
  src: url_css('fonts/ProximaNova/Bold/ProximaNova-Bold.eot');
  src: url_css('fonts/ProximaNova/Bold/ProximaNova-Bold.eot#iefix') format('embedded-opentype'),
  url_css('fonts/ProximaNova/Bold/ProximaNova-Bold.woff2') format('woff2'),
  url_css('fonts/ProximaNova/Bold/ProximaNova-Bold.svg') format('svg'),
  url_css('fonts/ProximaNova/Bold/ProximaNova-Bold.ttf') format('truetype'),
  url_css('fonts/ProximaNova/Bold/ProximaNova-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

/*social*/
@font-face {
  font-family: 'fontello';
  src: url_css('fonts/fontello/fontello.eot?98037504');
  src: url_css('fonts/fontello/fontello.eot?98037504#iefix') format('embedded-opentype'),
       url_css('fonts/fontello/fontello.woff2?98037504') format('woff2'),
       url_css('fonts/fontello/fontello.woff?98037504') format('woff'),
       url_css('fonts/fontello/fontello.ttf?98037504') format('truetype'),
       url_css('fonts/fontello/fontello.svg?98037504#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
 font-family: "fontello";
 font-style: normal;
 font-weight: normal;
 speak: none;

 display: inline-block;
 text-decoration: inherit;
 width: 1em;
 margin-right: .2em;
 text-align: center;
 font-variant: normal;
 text-transform: none;
 line-height: 1em;
 margin-left: .2em;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
}

.icon-cancel:before { content: '\e800'; }
.icon-location:before { content: '\e801'; }
.icon-phone:before { content: '\e804'; }
.icon-facebook:before { content: '\f09a'; }
.icon-vkontakte:before { content: '\f189'; }
.icon-instagram:before { content: '\f32d'; }



